import { requestAPI } from './handler';
/**
 * Initialization data for the jupyter-server-extension extension.
 */
const plugin = {
    id: 'jupyter-server-extension:plugin',
    autoStart: true,
    activate: (app) => {
        console.log('JupyterLab MAAP DPS Server extension is activated!');
        requestAPI('get_example')
            .then(data => {
            console.log(data);
        })
            .catch(reason => {
            console.error(`The maap_jupyter_server_extension server extension appears to be missing.\n${reason}`);
        });
    }
};
export default plugin;
